.article-header {
  margin-bottom: calcRem(25);

  @include respond-above(sm) {
    margin-bottom: get-vw(79px);
  }

  &--wrapper {
    margin: auto;
    text-align: center;

    @include respond-above(sm) {
        width: get-vw(1470px);
        margin-top: get-vw(30px);
    }
  }

  &--banner {
    width: 100%;
    background-size: cover;
    background-position: center;
    height: calcRem(182);

    @include respond-above(sm) {
      height: get-vw(678px);
      margin-bottom: get-vw(37px);
    }
  }

  &--category {
    @include NeutraText-BookAlt;
    margin-top: calcRem(16);
    margin-bottom: calcRem(18);

    @include respond-above(sm) {
      font-size: get-vw(33px);
      line-height: get-vw(36px);
      margin-bottom: get-vw(72px);
    }
  }

  &--title {
    @include NeutraText-BookAlt;
    text-transform: uppercase;
    font-size: calcRem(36);
    line-height: calcRem(34);
    margin-bottom: 4px;

    @include respond-above(sm) {
      font-size: get-vw(94px);
      line-height: get-vw(90px);
      margin-bottom: 0;
    }
  }

  &--para {
    @include NeutraText-LightAlt;
    font-size: calcRem(22);
    letter-spacing: 0.5px;
    max-width: 90%;
    margin: auto;

    @include respond-above(sm) {
      max-width: 100%;
      font-size: get-vw(46px);
      line-height: get-vw(47px);
      letter-spacing: get-vw(1.61px);
    }
  }
}


.three-reason {

  @include respond-below(sm) {
    padding: calcRem(0, 18);
  }

  &--wrapper {
    margin: auto;

    @include respond-above(sm) {
      width: get-vw(1770px);
    }
  }

  &--title {
    text-align: center;
    @include NeutraText-DemiAlt;
    margin-bottom: calcRem(16);

    @include respond-above(sm) {
      font-size: get-vw(32px);
      line-height: get-vw(41px);
      margin-bottom: get-vw(40px);
    }
  }

  .reason-box {
      display: flex;
      width: 100%;
      position: relative;

      @include respond-above(sm) {
        height: get-vw(568px);
        // align-items: flex-start;
      }

      &--content {
        background: var(--color-tile-bg);
        flex: 1;
        display: flex;
        align-items: center;
        position: absolute;
        color: var(--color-text);
        width: calcRem(198);
        right: 0;

        @include respond-above(sm) {
          width: get-vw(1060px);
          min-height: get-vw(274px);
          padding-left: get-vw(50px);
          padding-right: get-vw(80px);
        }

        &__number {
          @include NeutraText-BoldAlt;

          @include respond-below(sm) {
            position: absolute;
            right: 0;
            top: 0;
            background: var(--color-primary);
            width: calcRem(79);
            height: calcRem(79);
            border-radius: 50%;
            font-size: calcRem(56);
            display: flex;
            align-items: center;
            justify-content: center;
          }

          @include respond-above(sm) {
            font-size: get-vw(187px);
            line-height: get-vw(89px);
          }
        }

        &__para {
          @include NeutraText-BookAlt;
          padding: calcRem(35, 28);
          font-size: calcRem(14);
          line-height: calcRem(16);

          @include respond-above(sm) {
            font-size: get-vw(29px);
            line-height: get-vw(35px);
            margin-left: get-vw(48px);
            padding: 0;
          }
        }
      }

      &--image {
        width: calcRem(154);
        height: calcRem(205);

        @include respond-above(sm) {
          width: get-vw(775px);
          height: 100%;
          z-index: -2;
          position: relative;
        }
      }

      &.odd {

        @include respond-above(sm) {
          justify-content: flex-end;
        }
          .reason-box--content {

            @include respond-above(sm) {
              left: 0;
              top: get-vw(40px);
            }
          }
      }

      &.even {
        justify-content: flex-start;

          @include respond-above(sm) {
            margin-top: get-vw(-200px);
          }

          .reason-box--content {

            @include respond-above(sm) {
              right: 0;
              top: inherit;
              bottom: get-vw(40px);
            }
          }
      }
  }
}


.wow-factor {

  &--wrapper {
    margin: auto;
    display: flex;
    align-items: center;
    position: relative;
    color: var(--color-text);

    @include respond-above(sm) {
      width: get-vw(1770px);
    }

    &.top {
      z-index: 2;

      @include respond-below(sm) {
        flex-direction: column;
      }
    }

    &.bottom {

      z-index: 1;
      @include respond-above(sm) {
        margin-top: get-vw(-40px);
      }
    }
  }

  &--left {
    background: var(--color-tile-bg);
    position: relative;
    @include NeutraText-DemiAlt;
    text-align: center;
    padding: calcRem(35);
    width: calcRem(333);
    font-size: calcRem(16);
    line-height: calcRem(28);


    @include respond-above(sm) {
      width: get-vw(670px);
      font-size: get-vw(32px);
      line-height: get-vw(55px);
      padding-top: get-vw(98px);
      padding-right: get-vw(72px);
      padding-bottom: get-vw(53px);
      padding-left: get-vw(72px);
      margin-left: get-vw(126px);
      margin-top: get-vw(-154px);
    }

    .cta-button {
      position: absolute;
      top: calcRem(-22px);
      left: calcRem(40);
      width: calcRem(45);
      height: calcRem(45);

      @include respond-above(sm) {
        width: get-vw(127px);
        height: get-vw(127px);
        top: get-vw(-40px);
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &--right {
    background: var(--color-tile-bg);
    position: relative;
    @include NeutraText-DemiAlt;
    text-align: center;
    margin-top: calcRem(54);
    padding: calcRem(40, 44, 22);
    font-size: calcRem(17);
    line-height: calcRem(30);

    @include respond-above(sm) {
      width: get-vw(682px);
      font-size: get-vw(44px);
      line-height: get-vw(72px);
      padding-top: get-vw(98px);
      padding-right: get-vw(72px);
      padding-bottom: get-vw(53px);
      padding-left: get-vw(72px);
      margin-left: get-vw(146px);
      margin-top: get-vw(154px);
    }

    .cta-button {
      position: absolute;
      width: calcRem(45);
      height: calcRem(45);
      top: calcRem(-22px);
      left: 50%;
      transform: translateX(-50%);

      @include respond-above(sm) {
        top: get-vw(-40px);
        width: get-vw(127px);
        height: get-vw(127px);
      }
    }
  }

  &--banner {
    width: 100%;
    height: calcRem(211);
    margin-top: calcRem(-20);

    @include respond-above(sm) {
      height: get-vw(753px);
      margin-bottom: get-vw(90px);
      margin-top: 0;
    }

    img {
      object-fit: cover;
      height: 100%;
    }
  }
}


