.image-content {

  @include respond-above(sm) {
    margin-top: get-vw(78px);
    margin-bottom: get-vw(123px);
  }

  &--wrapper {
    margin: auto;

    @include respond-above(sm) {
      width: get-vw(1280px);
    }
  }

  &--image {

    @include respond-above(sm) {
      width: get-vw(560px);
      height: get-vw(560px);
    }

    img {
      object-fit: cover;
      height: 100%;
    }
  }

  &--content {
    background: var(--color-tile-bg);
    color: var(--color-text);
    position: relative;

    @include respond-above(sm) {
      width: get-vw(577px);
      padding-top: get-vw(52px);
      padding-right: get-vw(52px);
      padding-bottom: get-vw(52px);
      padding-left: get-vw(52px);
    }

    &__head {
      @include NeutraText-BoldItalicAlt;

      @include respond-above(sm) {
        font-size: get-vw(78px);
        margin-bottom: get-vw(36px);
      }
    }

    &__para {
      @include NeutraText-BookAlt;

      @include respond-above(sm) {
        font-size: get-vw(20px);
        line-height: get-vw(24px);
      }
    }
  }

  .odd {
    display: flex;
    width: 100%;
    align-items: center;

    @include respond-above(sm) {
      margin-bottom: get-vw(44px);
    }

    .image-content--content {

      @include respond-above(sm) {
        margin-left: get-vw(64px);
      }
    }
  }

  .even {
    display: flex;
    width: 100%;
    align-items: center;

    @include respond-above(sm) {
      flex-direction: row-reverse;
      margin-bottom: get-vw(44px);
    }

    .image-content--content {

      @include respond-above(sm) {
        margin-right: get-vw(64px);
      }
    }
  }

  &.image-content-gif {

    .image-content--image {

      @include respond-above(sm) {
        width: get-vw(640px);
        height: get-vw(640px);
      }
    }

    .image-content--content {
      background: transparent;
      color: var(--color-text);
      position: relative;
      text-align: center;
      margin: 0;

      @include respond-above(sm) {
        width: get-vw(640px);
      }

      &__title {
        @include NeutraText-BoldAlt;

        @include respond-above(sm) {
          font-size: get-vw(21px);
          line-height: get-vw(34px);
          margin-bottom: get-vw(30px);
        }
      }

      &__tag {
        @include NeutraText-BookItalicAlt;

        @include respond-above(sm) {
          font-size: get-vw(21px);
          line-height: get-vw(34px);
        }
      }

      .content-inner {

        @include respond-above(sm) {
          position: absolute;
          width: get-vw(390px);
          left: 50%;
          top: 50%;
          transform: translateX(-50%) translateY(-50%);
        }
      }
      .quote-left {
        position: absolute;

        @include respond-above(sm) {
          width: get-vw(54px);
          top: get-vw(77px);
          left: get-vw(77px);
        }
      }

      .quote-right {
        position: absolute;

        @include respond-above(sm) {
          width: get-vw(54px);
          bottom: get-vw(77px);
          right: get-vw(77px);
        }
      }
    }

    .odd {
      margin-bottom: 0;
      align-items: stretch;
    }

    .even {
      margin-bottom: 0;
      align-items: stretch;
    }
  }
}
